main {
  margin: 40px 30px;
  display: grid;
  grid-template-columns: 3fr 1.5fr;
  grid-template-rows: 1fr 1fr;
  column-gap: 30px;
  row-gap: 0.5vw;
}

.iframeContainer {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
}

.iframeContainer .iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

aside {
  display: flex;
  flex-direction: column;
  grid-column-start: 2;
  grid-row: 1 / 3;
  margin-bottom: 20px;
  height: 800px;
}

.more {
  display: flex;
  position: sticky;
  top: 0;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  padding-bottom: 0;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: none;
}

.more div {
  height: 30px;
  line-height: 30px;
}

section {
  display: flex;
  flex-direction: column;
}

.tags {
  font-size: 12px;
  color: rgb(60, 100, 206);
  margin-bottom: 10px;
}

.title {
  font-size: 1.5em;
  font-weight: 600;
  margin-bottom: 10px;
}

.upload {
  font-size: 14px;
  margin-bottom: 10px;
}

.upload span {
  padding-right: 10px;
  font-weight: 600;
}

@media screen and (max-width: 1100px) {
  main {
    display: flex;
    flex-direction: column;
  }

  aside {
    margin: 20px 0;
    max-height: 400px;
    height: auto;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }

  .more {
    display: flex;
    position: sticky;
    top: 0;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: none;
  }
}
